
$primary-color: #575858;
$secondary-color: #c3f2f2;
$background-color: #101111;
$card-bg: #ffffff;

// Mixin
@mixin shadow-effect {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    margin: 0;
    padding: 0;
    background-color: $background-color;
    color: #0c0c0c;
}

header {
    background-image: url(../../static/img/bg.jpg);
    color:rgb(244, 252, 242);
    font-weight: 700;
    font-display: optional;
    padding: 10px;
    text-align: center;
    text-shadow: -12px 4px 2px rgba(7, 138, 20, 0.6);  

    h1 {
        margin-bottom: 5px;
    }
}

main {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}
.card {
    background-color: $card-bg;
    color: #000;
    font-weight: 700;
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    @include shadow-effect;
    
    &:hover {
        transform: translateY(-2px);
        box-shadow: 2px 2px 4px #E9F4FF;
    }

    h2 {
        color: $primary-color;
        border-bottom: 2px solid rgb(13, 145, 39);
        box-shadow: 0 1px black;
        box-shadow: inset;
        padding-bottom: 10px;
        margin-bottom: 15px;
    }

     
    article {
        margin-top: 15px;
    }

    ul {
        list-style-type: 'üëâ ';
        padding-left: 20px;
    }
    button{
        box-shadow:2px 2px 4px #E9F4FF;
        text-shadow: 1px 1px 1px black;
    }
}

footer {
    text-align: center;
    padding: 10px;
    background-color: #807d7d;
    color: #333;
    bottom: 0;
    width: 100%;
    text-shadow:2px 2px 4px #42f41e;
    font-weight: 600;
}
.open-modal-btn {
    color: black;
    font-weight: 800;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px;
    border:2px #000000 solid;
    // box-shadow: 2px 2px 4px white;
    background: transparent;
    
    
    &:hover {
        box-shadow: 3px 3px 7px black;;
        text-shadow: none;
    }
}

.modal {
    display: none; 
    position: fixed; 
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto; 
    background-color: rgba(0, 0, 0, 0.4);
    color: #000;
    
    h4{
       color: #000;
       font-weight: 600;
       text-shadow:1px 1px 2px #060606;
    }
}

.modal-content {
    background-color: #fefefe;
    margin: 10% auto; 
    padding: 30px;
    border: 1px solid #888;
    width: 80%;
    border-radius: 8px;
    position: relative;
    @include shadow-effect;
     color: #000;
    font-weight: 700;
    
    h3 {
        color: $primary-color;
        text-shadow:1px 1px 2px #060606;
    }
}

.close-btn {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    
    &:hover,
    &:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
    }
}

.mrg{
    margin-bottom: 80px;
}


.step-slider-container {
    position: relative;
    padding: 20px;
    margin: 20px auto;
    border: 1px dashed $secondary-color;
    border-radius: 6px;
    min-height: 200px;
    overflow: hidden;
}

.auth-step {
    display: none; 
    padding: 0 40px; 
    text-align: center;
    
    h3 {
        color: $primary-color;
    }
    img{
        min-width: 150px;
        max-width: 250px;

    }
}

.current-step {
    display: block;
}

.step-icon{
    font-size: 4em;
    display: block;
    margin-bottom: 10px;
}

.prev-step, .next-step {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 10px;
    margin-top: -30px;
    color: $primary-color;
    font-weight: bold;
    font-size: 25px;
    user-select: none;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 4px;
    transition: background-color 0.3s;
}

.next-step {
    right: 10px;
}

.prev-step {
    left: 10px;
}

.prev-step:hover, .next-step:hover {
    background-color: $secondary-color;
    color: white;
}

